export const LabHeader = ({num, children}) => {
	return (
		<div className="text-center">
			<h1 className='text-center font-bold text-xl'>   {num}</h1>

			<p className='mt-2'>
				<strong>:</strong> "{children}"
			</p>
			<p className='mt-2'>
				<strong> 10</strong>
			</p>
		</div>
	);
};
